* {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  text-decoration: none;
  outline: none;
}

body .app {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  background-color: #fff;
}

body .app-header {
  height: 60px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

body .navigation {
  width: 50%;
  height: auto;
}

body .navigation .search-box {
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  background-color: rgba(202, 202, 202, 0.489);
}

body .navigation .search-box .search-input {
  background-color: transparent;
  width: 100%;
  height: 100%;
  outline: none;
  border: none;
  color: #000;
}

body .navigation .search-box :nth-child(1) {
  margin: 0 10px 0 10px;
  color: #b3b3b3;
  font-size: 1.5em;
  cursor: pointer;
}

body .navigation .search-box :nth-child(3) {
  margin: 0 15px 0 10px;
  color: #b3b3b3;
  font-size: 1.2em;
  cursor: pointer;
}

body .container-title {
  width: 100%;
  height: 80px;
  margin-top: 20px;
}

body .container-title .title {
  margin-left: 5vw;
  font-size: 2em;
}

body .container-photo {
  height: auto;
  width: 90%;
  margin: auto;
}

body .container-photo .card:hover img {
  cursor: pointer;
  opacity: 0.8;
}

body .container-photo .card img {
  width: 100%;
  height: 100%;
}

body .photo-details {
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: rgba(50, 50, 50, 0.959);
}

body .photo-details .details-avatar {
  width: 100%;
  height: auto;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(50, 50, 50, 0.959);
}

body .photo-details .details-avatar .info-photo {
  width: auto;
  height: auto;
}

body .photo-details .details-avatar .info-photo .info {
  margin-top: 5px;
  padding-left: 5%;
  padding-bottom: 10px;
  font-size: 1.3em;
  color: #fff;
}

body .photo-details .details-avatar .exit {
  color: #fff;
  font-size: 40px;
  position: absolute;
  right: 0;
  top: 0;
  margin-right: 20px;
  margin-top: 10px;
  cursor: pointer;
}
